@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
/** @format */
.App {
  text-align: center;
  font-family: "Staatliches", cursive;
}
h2 {
  margin: 30px;
  padding: 30px;
}

/** @format */

.customers-list {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 15px;
}
.customer-card {
  padding: 15px;
  margin: 10px;
}

/** @format */

.vehicles-list {
  display: grid;
  grid-template-columns: auto auto auto auto;
  text-align: justify;
}
.vehicle-card {
  padding: 15px;
  margin: 10px;
}

/** @format */

h1 {
  font-weight: 300;
  font-size: 60px;
  line-height: 1.2;
  margin-bottom: 15px;
}

.showcase {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding: 0, 20px;
  color: white;
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.video-container video {
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.video-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.content {
  z-index: 10;
}
.logo-img {
  width: 8vw;
}

